import { Box, IconButton, Image, Text } from "@chakra-ui/react"
import { PictureIcon } from "../../../../components/Icons/Icons"
import { convertUTCTimeToLocalTime } from "../../../../utils/date"

export default function ImageDetail({ id, src, onOpen, date, setSelectedImageId }) {
    const _handleImageDetail = () => {
        setSelectedImageId(id)
        onOpen()
    }

    return (
        <Box
            borderWidth="1px"
            rounded="lg"
            shadow="lg"
            position="relative">
            <IconButton
                position="absolute"
                bottom={2}
                right={2}
                bg="whibluepha.300"
                icon={<PictureIcon />}
                onClick={_handleImageDetail}
            />
            <Text align="right">{(convertUTCTimeToLocalTime(date))?.toLocaleString()}</Text>
            <Image
                key={id}
                h='200px'
                w={"100%"}
                alt={`Picture`}
                src={src}
                roundedTop="lg" />
        </Box>
    )
}